/* eslint-disable no-prototype-builtins */
import axios from 'axios';
const gasUrl = 'https://script.google.com/macros/s/AKfycbwnBtdWx7wQQXuWVjMby23PeQwytDmT22V597jWX_-yPoK82kEa/exec';

const userRequest = axios.create({
  baseURL: gasUrl,
  headers: { 'Content-Type': 'application/json' },
});
userRequest.defaults.timeout = 2500;

export const doPost = (data) => {
  return userRequest
    .post('/exec', JSON.stringify(data))
    .then((res) => console.log(res))
    .catch((err) => console.log(err.message));
};

export const doGet = () => userRequest.get('/exec');

export const encodeFormData = (data) => {
  if (!data) return ''; // Always return a string
  var pairs = []; // To hold name=value pairs
  for (var name in data) {
    // For each name
    if (!data.hasOwnProperty(name)) continue; // Skip inherited
    if (typeof data[name] === 'function') continue; // Skip methods
    var value = data[name].toString(); // Value as string
    name = encodeURIComponent(name.replace(' ', '+')); // Encode name
    // value = encodeURIComponent(value.replace(" ", "+")); // Encode value
    value = encodeURIComponent(value); // Encode value
    pairs.push(name + '=' + value); // Remember name=value pair
  }
  return pairs.join('&'); // Return joined pairs separated with &
};
export const xhrGet = (url, header = false) =>
  new Promise((resolve, reject) => {
    var xhr = new XMLHttpRequest();
    xhr.open('GET', url + (/\?/.test(url) ? '&' : '?') + new Date().getTime());
    if (!header) {
      for (var h in header) {
        xhr.setRequestHeader(h, header[h]);
      }
    }
    xhr.onload = () => {
      if (xhr.status >= 200 && xhr.status < 400) {
        // console.table(JSON.parse(xhr.response));
        resolve(JSON.parse(xhr.response));
      } else {
        reject(new Error(`連結失敗${xhr.status}`));
      }
    };
    xhr.send(null);
  });
export const xhrPost = (data, url = gasUrl, header = false) =>
  new Promise((resolve, reject) => {
    console.log('xhrPost', data);
    const xhr = new XMLHttpRequest();
    xhr.open('POST', url);
    if (!header) {
      for (var h in header) {
        xhr.setRequestHeader(h, header[h]);
      }
    }
    xhr.setRequestHeader('Content-type', 'application/x-www-form-urlencoded; charset=UTF-8');
    xhr.onload = () => {
      if (xhr.status >= 200 && xhr.status < 400) {
        // console.table(JSON.parse(xhr.response));
        resolve(JSON.parse(xhr.response));
      } else {
        reject(new Error(`連結失敗${xhr.status}`));
      }
    };
    xhr.send(encodeFormData(data));
  });

export const formatInput = (title, name, validate, error = '', helpText = '', html = '') => ({
  title,
  name,
  validate,
  error,
  helpText,
  html,
  value: '',
});
