import Vue from 'vue';
import VueRouter from 'vue-router';

import Home from './page/Home';
import Appointment from './page/Appointment';

Vue.use(VueRouter);

const routes = [
  {
    name: 'home',
    path: '/',
    component: Home,
  },
  {
    name: 'appointment',
    path: '/line',
    component: Appointment,
  },
];

const Router = new VueRouter({
  mode: 'history',
  routes,
});
export default Router;
