<template lang="pug">
.about
  .container
    h3 {{ title }}
    p(v-for="line in content") {{ line }}
      //- a.button.button-primary(href='http://getskeleton.com') 預約服務
</template>

<script>
export default {
  name: "About",
  data() {
    return {
      title: "",
      content: [],
    };
  },
  props: ["items"],
  watch: {
    items(ori) {
      ori.forEach((item) => {
        const [title, content] = item;
        if (title.includes("名稱")) {
          this.title = content;
        }
        if (title.includes("內容")) {
          this.content.push(content);
        }
      });
      // code
    },
  },
};
</script>
