<template lang="pug">
.contact
  .container(v-if="oid")
    h3 謝謝你填寫資料，我會盡快與你連繫
  .container(v-if="!oid")
    h3 😺🐶聯絡我！🐶😺
    .row
      #userEmailBox.six.columns
        label(for="userEmail") Email(必填)
        input#userEmail.u-full-width(
          type="email",
          placeholder="test@mailbox.com",
          v-model="user.email"
        )
      .six.columns
        label(for="userName") 預約人姓名(必填)
        input#userName.u-full-width(
          type="text",
          placeholder="林小芳",
          v-model="user.name"
        )
    .radio-box(v-for="(item, i) in radioList")
      label {{ item.label }}
      .radioInput(v-for="(ip, i) in item.options")
        input(
          :id="`${item.key}${i}`",
          v-bind:value="ip",
          type="radio",
          v-model="user[item.key]"
        )
        label(:for="`${item.key}${i}`") {{ ip }}
    label(for="userContent") 毛小孩資料 / 其他問題(必填)
    textarea#userContent.u-full-width(
      placeholder="毛小孩名字/性別/年齡",
      v-model="user.content"
    )
    input.button-primary(type="button", value="立即預約!", v-on:click="submit()")
</template>
<script>
import { xhrPost } from "../fun.js";
export default {
  name: "Contact",
  data() {
    return {
      oid: "",
      user: {
        name: "",
        email: "",
        content: "",
        contact: "",
        talk: "",
        time: "",
      },
      radioList: [
        {
          key: "contact",
          label: "語音工具選擇",
          options: ["Line官方", "FB Messager", "Google Meet"],
        },
        {
          key: "talk",
          label: "溝通類型",
          options: ["一般", "緊急", "離世"],
        },
        {
          key: "time",
          label: "溝通時間",
          options: ["30分鐘", "60分鐘"],
        },
      ],
    };
  },
  watch: {
    "user.contact"(data) {
      console.log(data);
    },
  },
  methods: {
    submit() {
      const { name, email, content } = this.user;
      if (!name || !email || !content) {
        console.log("content error");
        return;
      }
      console.log(this.user);
      xhrPost({
        actions: "POST",
        table: "預約清單",
        data: JSON.stringify(this.user),
      }).then((response) => {
        this.oid = response.data;
        console.info("response", response);
      });
    },
  },
};
</script>

<style scoped>
.radioInput,
.radioInput label {
  display: inline;
}
</style>
