<template lang="pug">
.price
  .container
    .row
      .one-third.column.value.item(v-for="item in price", v-if="item")
        h2.value-multiplier {{ item.title }}
        .time(v-if="item.times", v-for="time in item.times")
          h5.value-heading(v-if="time.title") {{ time.title }}
          h6(v-if="time.sub") {{ time.sub }}
          p.value-description {{ time.price }}
    .row
      .column
        p(v-for="line in tipText") {{ line }}
</template>

<script>
export default {
  name: "Price",
  data() {
    return {
      tipText: [],
      price: [],
    };
  },
  props: ["items"],
  watch: {
    items(ori) {
      ori.forEach((item) => {
        const [title, content] = item;
        if (title.includes("備註文字")) {
          this.tipText.push(content);
        }
        if (title.includes("項目")) {
          const [, number, type] = title.split("-");
          if (!this.price[number]) {
            this.price[number] = {
              title: "",
              times: [],
            };
          }
          if (type === "名稱") {
            this.price[number]["title"] = content;
          }
          if (type === "內容") {
            const times = content.split(", ");
            this.price[number]["times"].push({
              title: times[0],
              sub: times[1],
              price: times[2],
            });
          }
        }
      });
      // code
    },
  },
};
</script>
