export default {
  name: 'LiffMixin',
  data() {
    return {
      liffId: '1554258865-JqBmdgd8',
    };
  },
  created() {},
  methods: {
    initLiff() {
      // code
      this.$liff
        .init({
          liffId: this.liffId,
        })
        .then(async () => {
          const isClient = this.$liff.isInClient();
          const isLoggedIn = this.$liff.isLoggedIn();

          this.$store.commit('setLiffInClient', isClient);
          this.$store.commit('setLiffLoggedIn', isLoggedIn);

          if (isLoggedIn) {
            this.$liff
              .getProfile()
              .then((res) => this.$store.commit('setLiffUserProfile', res))
              .catch(function(err) {
                console.info('getProfileDie error: ', err);
              });
          }
          if (!isLoggedIn && !isClient) {
            this.$liff.login();
          }
        })
        .catch((err) => {
          // alert(err);
          console.info('initLiff error: ', err);
        });
    },
    liffSendMessage(text) {
      if (!this.$liff.isInClient()) {
        console.info('test', text);
        return;
      }
      this.$liff
        .sendMessages([
          {
            type: 'text',
            text,
          },
        ])
        .then(function() {})
        .catch(function(error) {
          // window.alert('Error sending message: [' + error + ']');
          console.info('sendMessages error: ', error);
        });
    },
  },
};
