<template lang="pug">
.lineLiffContact
  .container(v-if="oid")
    h3 謝謝你填寫資料，我會盡快與你連繫
  .container(v-if="!oid")
    h3 😺🐶預約溝通！🐶😺
    label(for="userName") 預約人姓名(必填)
    input#userName.u-full-width(
      type="text",
      placeholder="林小芳",
      v-model="user.name"
    )
    .radio-box(v-for="(item, i) in radioList")
      label {{ item.label }}
      .radioInput(v-for="(ip, i) in item.options")
        input(
          :id="`${item.key}${i}`",
          v-bind:value="ip",
          type="radio",
          v-model="user[item.key]"
        )
        label(:for="`${item.key}${i}`") {{ ip }}
    label(for="userContent") 毛小孩資料 / 其他問題(必填)
    textarea#userContent.u-full-width(
      placeholder="毛小孩名字/性別/年齡",
      v-model="user.content"
    )
    input.button-primary(type="button", value="立即預約!", v-on:click="sendMsg()")
</template>
<script>
import { xhrPost } from "../fun.js";
import LiffMixin from "../liffMixin";
export default {
  name: "Contact",
  mixins: [LiffMixin],
  data() {
    return {
      oid: "",
      user: {
        name: "",
        content: "",
        talk: "",
        time: "",
      },
      radioList: [
        {
          key: "talk",
          label: "溝通類型",
          options: ["一般", "緊急", "離世"],
        },
        {
          key: "time",
          label: "溝通時間",
          options: ["30分鐘", "60分鐘"],
        },
      ],
    };
  },
  watch: {
    "user.contact"(data) {
      console.log(data);
    },
  },
  methods: {
    submit() {
      const { name, email, content } = this.user;
      if (!name || !email || !content) {
        console.log("content error");
        return;
      }
      console.log(this.user);
      xhrPost({
        actions: "POST",
        table: "預約清單",
        data: JSON.stringify(this.user),
      }).then((response) => {
        this.oid = response.data;
        console.info("response", response);
      });
    },
    sendMsg() {
      const { name, talk, time, content } = this.user;
      const text =
        "預約人姓名: " + name + "\n" + talk + " " + time + "\n" + content;
      this.liffSendMessage(text);
      this.$liff.closeWindow();
    },
  },
};
</script>

<style scoped>
.radioInput,
.radioInput label {
  display: inline;
}
</style>