<template lang="pug">
.section.values(style=`background-image: url(${{ bgUrl }})`)
  .container
    .row
      .column
        h2 {{ title }}
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      title: "",
      bgUrl: "/css/images/flowers-5383054_1920.jpg",
    };
  },
  props: ["items"],
  watch: {
    items(ori) {
      ori.forEach((item) => {
        const [title, content] = item;
        if (title.includes("名稱")) {
          this.title = content;
        }
        if (title.includes("設定圖片")) {
          this.bgUrl = content;
        }
      });
      // code
    },
  },
  created() {},
  methods: {},
};
</script>

