<template lang="pug">
.precautions
  .container
    h3 {{ title }}
    p(v-for="line in content") {{ line }}
</template>

<script>
export default {
  name: "Precautions",
  data() {
    return {
      title: "",
      content: [],
    };
  },
  created() {},
  props: ["items"],
  watch: {
    items(ori) {
      ori.forEach((item) => {
        const [title, content] = item;
        if (title.includes("名稱")) {
          this.title = content;
        }
        if (title.includes("內容")) {
          this.content.push(content);
        }
      });
      // code
    },
  },
  methods: {
    setContent() {
      // code
    },
  },
};
</script>
