import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    liffLoggedIn: false,
    liffInClient: false,
    liffUserProfile: {},
    count: 0,
  },
  mutations: {
    setLiffLoggedIn(state, data) {
      // console.info('date', data);
      state.liffLoggedIn = data;
    },
    setLiffInClient(state, data) {
      state.liffInClient = data;
    },
    setLiffUserProfile(state, data) {
      state.liffUserProfile = data;
    },
  },
});

export default store;
