<template lang="pug">
div
  Header(:items="header")
  About(:items="about")
  Precautions(:items="precautions")
  Price(:items="price")
  Contact
  Footer
</template>

<script>
import Header from "../components/Header.vue";
import About from "../components/About.vue";
import Precautions from "../components/Precautions.vue";
import Price from "../components/Price.vue";
import Contact from "../components/Contact.vue";
import Footer from "../components/Footer.vue";
import { xhrPost } from "../fun.js";

export default {
  name: "Home",
  components: { Header, About, Precautions, Price, Contact, Footer },
  data() {
    return {
      sourceData: [],
      header: [],
      about: [],
      precautions: [],
      price: [],
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      xhrPost({ table: "設定內容", actions: "GET" }).then((response) => {
        const { data } = response;
        if (data.length > 0) {
          data.forEach((item) => {
            const [title] = item;
            ["header", "about", "precautions", "price"].forEach((pageTitle) => {
              if (title.includes(pageTitle)) {
                this[pageTitle].push(item);
              }
            });
          });
        }
      });
      // code
    },
  },
};
</script>




